import React, {
  DetailedHTMLProps,
  HTMLAttributes,
  ReactElement
} from 'react';
import cn from 'classnames';

import { CellProps } from './Cell';

import styles from '../Table.module.scss';

export interface RowProps extends DetailedHTMLProps<HTMLAttributes<HTMLTableRowElement>, HTMLTableRowElement> {
  /** Выбрана ли строка? */
  selected?: boolean;

  /** Дочерние элементы */
  children?: ReactElement<CellProps> | ReactElement<CellProps>[];
}

export function Row({
  className,
  children,
  selected,
  ...rest
}: RowProps): JSX.Element {
  const classes = cn(className, { [styles.selected_row]: selected });

  return (
    <tr
      { ...rest }
      className={ classes }
    >
      { children }
    </tr>
  );
}
